type BoltAction = {
  type: string;
  filePath: string;
  content: string;
};

export default function UserBlock() {
  const { allMessages, setAllMessages } = useContext(userContext)!;

  function xmlToString(xml: string) {
    const parserForXml = new XMLParser({
      ignoreAttributes: false,
      attributeNamePrefix: "",
      textNodeName: "content",
      trimValues: true,
    });

    const parsedXML = parserForXml.parse(xml);
    let parsedBoltActions: BoltAction[];

    if (Array.isArray(parsedXML.boltArtifact.boltAction)) {
      parsedBoltActions = parsedXML.boltArtifact.boltAction;
    } else {
      parsedBoltActions = [parsedXML.boltArtifact.boltAction];
    }

    let allSteps: Step[] = [];

    for (let i = 0; i < parsedBoltActions.length; i++) {
      let newStep: Step = {
        title: parsedBoltActions[i].filePath,
        status: "Pending",
        content: parsedBoltActions[i].content,
      };

      allSteps.push(newStep);
    }

    return allSteps;
  }

  async function promptResponseFromAi() {
    try {
      const response = await axiosInstance.post("/take-prompt", {});
      const correctedXML = xmlToString(xml);
      console.log(correctedXML);
      let dummyMessages = [...allMessages!];
      dummyMessages[dummyMessages?.length - 1].ai = correctedXML!;
      // allMessages = dummyMessages;
      console.log(dummyMessages);
      setAllMessages(dummyMessages);
    } catch (err) {
      toast.error("Ai Model is not Running");
    }
  }

  useEffect(() => {
    promptResponseFromAi();
  }, [allMessages]);

  return (
    <div>
      <Card>
        <CardContent>
          {allMessages
            ? allMessages.map((item, index) => (
                <div className="flex flex-col gap-1" key={index}>
                  <div className="flex justify-end">
                    <span>{item.user}</span>
                  </div>

                  <div className="flex justify-start">
                    {item.ai.map((newItem, index) => (
                      <Card key={index}>
                        <CardContent className="flex gap-3">
                          {newItem.status == "Pending" ? (
                            <TicketCheckIcon />
                          ) : (
                            <ListChecksIcon />
                          )}
                          {newItem.title}
                        </CardContent>
                      </Card>
                    ))}
                  </div>
                </div>
              ))
            : ""}
        </CardContent>
      </Card>
    </div>
  );
}
